import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import {  registerAsync, setCredentials ,selectAuth} from '../../context/auth/authSlice'
import { useDispatch ,useSelector} from 'react-redux';

function Copyright(props) {
    return (
        <Typography variant="body2" color="text.secondary" align="center" {...props}>
            {'Copyright © '}
            <Link color="inherit" href="https://mui.com/">
                Esc far away
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

const defaultFormFields = {

    email: '',
    password: '',
    userName: '',
};

export default function Register({ Register }) {

    const handelRegister = () => {
        Register()
    }

    const dispatch = useDispatch()
    const {registered} = useSelector(selectAuth)
    const [formFields, setFormFields] = React.useState(defaultFormFields)

    const restFormFields = () => {
        setFormFields(defaultFormFields)
    }

    const handelChange = (event) => {
        const { name, value } = event.target;
        setFormFields({ ...formFields, [name]: value })
        
    }

    const handleSubmit = async (event) => {
        event.preventDefault();
        dispatch(setCredentials(formFields))
        dispatch(registerAsync())
        Register()
    };

 


    return (
        <Container component="main" maxWidth="xs">
            <Box
                sx={{
                    marginTop: 8,
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
                    <LockOutlinedIcon />
                </Avatar>
                <Typography component="h1" variant="h5">
                    Register
                </Typography>
                <Box component="form" onSubmit={handleSubmit}  sx={{ mt: 1 }}>
                    <TextField
                        type={'email'}
                        margin="normal"
                        required
                        fullWidth
                        id="email"
                        label="Email Address (will be case sensitive)"
                        name="email"
                        autoComplete="email"
                        autoFocus
                        onChange={handelChange}
                    />
                    <TextField
                        margin="normal"
                        required
                        fullWidth
                        id="username"
                        label="User name"
                        name="userName"
                        // autoComplete="text"

                        onChange={handelChange}
                    />
                    <TextField
                        margin="normal"
                        required
                        fullWidth
                        name="password"
                        label="Password"
                        type="password"
                        id="password"
                        autoComplete="current-password"
                        onChange={handelChange}

                    />

                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        sx={{ mt: 3, mb: 2 }}
                    >
                        Create account
                    </Button>
                    <Grid container>
                        <Grid item xs>
                            {/* <Link href="#" variant="body2">
                                Forgot password?
                            </Link> */}
                        </Grid>
                        <Grid item>
                            <Link onClick={handelRegister} variant="body2">
                                {"Already have an account?"}
                            </Link>
                        </Grid>
                    </Grid>
                </Box>
            </Box>
            <Copyright sx={{ mt: 8, mb: 4 }} />
        </Container>
    );
}